@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Dosis&family=Heebo&family=Poppins&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 62.5%;
}

body{
  background: rgb(3,0,31);
  color: white;
  font-family: 'Poppins', sans-serif;
}

.App{
  min-height: 100vh;
  display: flex;


}

.sidebar{
  flex: 3;
  border-right: 1px solid rgb(100, 100,100 );
}

.main{
  flex: 9;
}

.logo{
  margin-right: 1rem;
}

.brand{
  font-size: 2rem;
}

.upperSide{
  padding: 2.5rem;
  border-bottom: 1px solid rgb(100, 100,100 );
  height: 70%;
  
  position: fixed;
    top: 0; /* Adjust top, bottom, left, or right as needed */
    left: 0;
  /* display: flex; */
}

.upperSideTop{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2.5rem;
 
}

.upperSideTop img{
  height: 5rem;
  width: 5rem;
}

.addBtn{
  height: 2rem;
  padding-right: 1rem;
}

.midBtn{
  background: #5A4BFF;
  border: none;
  color: white;
  padding: 0.8rem;
  font-size: 1.5rem;
  width: 25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 3rem;
  border-radius: 0.5rem;
  cursor: pointer;

}

.midBtn img{
  height: 36px;
  width: 48px;
}

.query{
  display: flex;
  align-items: center;
  background: transparent;
  color: rgba(222,222,222,1);
  padding: 1.5rem;
  width: 25rem;
  margin: 1rem auto;
  border-radius: 0.5rem;
  border: 1px solid rgba(98,98,98,1);
  cursor: pointer;
}

.query>img{
  margin-right: 2rem;
  object-fit: cover;
  height: 1.75rem;
}


.lowerSide{
  padding: 2rem;
  position: fixed;
    bottom: 6rem;
  

}

.listItems{
  margin: 0.75rem;
  display: flex;
  align-items: center;
  font-size: 1.3rem;
}

.listitemsImg{
  margin: 1rem;
  padding-right: 1rem;
  height: 2.4rem;
  /* width: 2.4rem; */
}

.listItems:nth-child(1)>.listitemsImg{
  margin-right: 0.5rem;
}

.listItems:nth-child(3)>.listitemsImg{
  margin-right: 0.75rem;
}

.main{
  min-height: calc(100vh-14rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 6rem 10rem;
  margin-bottom: 0;
}

.chats{
  overflow: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  width: 200%;
  max-width: 80%;
  height: calc(100vh-17rem);
}

.chat{
  margin: 1rem;
  padding: 2rem 3rem;
  font-size: 1.2rem;
  display: flex;
  align-items: flex-start;
  text-align: justify;
}

.chatImg{
  object-fit: cover;
  width: 3.5rem;
  margin-right: 2rem;
  border-radius: 0.5rem;
}

.bot{
  background: rgba(28,30,58,1);
  width: fit-content;
  border-radius: 0.5rem;
}

.chatFooter{
  margin-top: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.inp{
  padding: 0.5rem;
  background: rgba(28,30,58,1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.5rem;
  width: 68rem;
}

.inp>input{
  width: calc(100%-3rem);
  outline: none;
  padding: 1.25rem;
  color: white;
}

.inp>input, .send{
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.chatFooter>p{
  margin: 2rem 0;
}

.txt{
  line-height: 2.4rem;
  color: rgba(222,222,222,1);
}